import Vue from 'vue'
import Router from 'vue-router'
import Main from '../views/Main.vue'
import Province from '../components/Province.vue'
import Country from '../components/Country.vue'
Vue.use(Router)

export default new Router({
  mode:'history',
  // base:'/dist/',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/province',
      name: 'Province',
      component: Province,
    },
    {
      path: '/country',
      name: 'Country',
      component: Country,
    },
  ]
})



