<template>
  <div>
    <el-table
      :cell-style="cellStyle"
      height="250"
      :data="list"
      style="width: 100%"
      @row-click="view"
    >
      <el-table-column prop="province" label="省份" width="180">
      </el-table-column>

      <el-table-column prop="confirm" label="累计确诊" width="180">
      </el-table-column>

      <el-table-column prop="heal" label="累计治愈"> </el-table-column>

      <el-table-column prop="confirm_day_add" label="昨日新增">
      </el-table-column>
    </el-table>

    <el-switch
      style="margin-top: 15%"
      v-model="type"
      @change="changetype"
      active-text="累计确诊"
      inactive-color="#13ce66"
      inactive-text="新增病例"
    >
    </el-switch>
    <div id="main" style="margin-top: 0%"></div>
  </div>
</template>
<script>
import echarts from "echarts";
import "echarts/map/js/china.js";
export default {
  data() {
    return {
      type: true,
      list: [],
      confirm_day_add: [],
      confirm: [],
    };
  },
  methods: {
    cellStyle() {
      return "cursor:pointer;";
    },
    view(val) {
      this.$router.push({ name: "Province", params: { row: val } });
    },
    changetype(val) {
      this.buildMap(val);
    },
    buildMap(type) {
      echarts.init(document.getElementById("main")).dispose();
      let myChart = echarts.init(document.getElementById("main"));
      let option = {
        title: {
          //地图显示标题
          text: "全国疫情地图",
        },
        tooltip: {
          formatter: function (params, ticket, callback) {
            if (type === true)
              return "地区: " + params.name + "<br/>" + "确诊:" + params.value;
            else
              return "地区: " + params.name + "<br/>" + "新增:" + params.value;
          }, //数据格式化
        },
        visualMap: {
          min: 0,
          max: this.type == true ? 10000 : 100,
          left: "left",
          top: "bottom",
          text: this.type == true ? ["10000", "0"] : ["100", "0"], //取值范围的文字
          inRange: {
            color: ["#fff4e6", "#ee2002"], //取值范围的颜色
          },
          show: true, //图注
        },
        geo: {
          map: "china",
          roam: false, //不开启缩放和平移
          zoom: 1.23, //视角缩放比例
          label: {
            normal: {
              show: true,
              fontSize: "10", //注意：地图省份名字字体如果过大会导致字体重叠
              color: "rgba(0,0,0,0.7)",
            },
          },
          itemStyle: {
            normal: {
              borderColor: "rgba(0, 0, 0, 0.2)",
            },
            emphasis: {
              areaColor: "#F3B329", //鼠标选择区域颜色
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              shadowBlur: 20,
              borderWidth: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
          },
        },
        series: [
          {
            name: "地区",
            type: "map",
            geoIndex: 0,
            data: this.type == true ? this.confirm : this.confirm_day_add,
          },
        ],
      };
      myChart.setOption(option);
      myChart.on("click", (params) => {
        for (let i in this.list) {
          if (this.list[i].name === params.data.name) {
            this.$router.push({
              name: "Province",
              params: { row: this.list[i] },
            });
            break;
          }
        }
      });
    },
  },
  mounted() {
    // this.buildMap(this.type);
  },
};
</script>
 
<style scoped>
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
#main {
  width: 600px;
  height: 450px;
  margin: 150px auto;
  border: none;
}
</style>


