<template>
  <div
    style="background:#00FFFF;
  background-repeat: no-repeat;
  background-size: cover;"
  >
    <el-date-picker
      style="margin-left: 65.5%"
      v-model="date"
      value-format="yyyy-MM-dd"
      type="date"
      @change="changedate"
      placeholder="选择日期"
    >
    </el-date-picker>

    <el-tabs
      v-model="tab"
      @tab-click="changeid"
      style="margin-left: 10%; margin-right: 10%"
      v-loading="loading"
      type="border-card"
    >

      <el-tab-pane label="国内疫情">
        <china ref="china"></china>
      </el-tab-pane>

      <el-tab-pane label="国外疫情">
        <world ref="world"></world>
      </el-tab-pane>

      <div v-show="id === 'line'">
        <span style="font-weight: bold">国内疫情走势</span>
        <div id="line"></div>
      </div>

      <div v-show="id === 'line1'">
        <span style="font-weight: bold">国际疫情走势</span>
        <div id="line1"></div>

        <span style="font-weight: bold">各大洲疫情走势</span>
        <div id="z"></div>
      </div>

    </el-tabs>
  </div>
</template>

<script>
import China from "../components/China.vue";
import World from "../components/World.vue";
import echarts from "echarts";
export default {
  components: { World, China },
  data() {
    return {
      tab: "0",
      id: "line",
      date: null,
      loading: false,
      date: null,
      world: [],
      china: [],
    };
  },
  methods: {
    changeid(x) {
      if (x.index == 0) {
        this.id = "line";
        this.buildline();
      } else this.id = "line1";
    },
    changedate(val) {
      if (val < "2021-01-01") this.date = "2021-01-01";
      if (val > "2021-12-15") this.date = "2021-12-15";
      this.$axios
        .all([
          this.$axios.get("../../static/world/json/" + this.date + ".json"),
          this.$axios.get("../../static/china/json/" + this.date + ".json"),
        ])
        .then(
          this.$axios.spread((resp1, resp2) => {
            this.world = resp1.data;
            this.china = resp2.data;
          })
        )
        .then(() => {
          this.$refs.china.list = this.china;
          for (let i in this.china) {
            this.china[i].name = this.china[i].province;
            this.$refs.china.confirm_day_add[i] = {
              name: this.china[i].name,
              value: this.china[i].confirm_day_add,
            };
            this.$refs.china.confirm[i] = {
              name: this.china[i].name,
              value: this.china[i].confirm,
            };
          }
          this.$refs.china.buildMap(true);
        })
        .then(() => {
          this.$refs.world.list = this.world;
          for (let i in this.world) {
            this.world[i].name = this.world[i].location;
            this.$refs.world.confirm_day_add[i] = {
              name: this.world[i].name,
              value: this.world[i].new_cases,
            };
            this.$refs.world.confirm[i] = {
              name: this.world[i].name,
              value: this.world[i].total_cases,
            };
          }
          this.$refs.world.initChart();
          this.loading = false;
        });
    },
    buildline() {
      var confirm_day_add = null;
      var suspect_day_add = null;
      var dead_day_add = null;
      var heal_day_add = null;
      this.$axios
        .all([
          this.$axios.get("../../static/china/dates/confirm_day_add.json"),
          this.$axios.get("../../static/china/dates/dead_day_add.json"),
          this.$axios.get("../../static/china/dates/heal_day_add.json"),
          this.$axios.get("../../static/china/dates/suspect_day_add.json"),
        ])
        .then(
          this.$axios.spread((...resp) => {
            var dates = resp[0].data.data;
            confirm_day_add = resp[0].data.value;
            dead_day_add = resp[1].data.value;
            heal_day_add = resp[2].data.value;
            suspect_day_add = resp[3].data.value;

            echarts.init(document.getElementById("line")).dispose();
            let echartsOption = {
              legend: {
                //图表上方的图例
                data: ["新增治愈", "新增疑似", "新增确诊", "新增死亡"],
              },
              xAxis: {
                type: "category",
                data: dates, // x轴数据
                name: "日期", // x轴名称
                nameTextStyle: {
                  // x轴名称样式
                  fontWeight: 600,
                  fontSize: 18,
                },
              },
              yAxis: {
                type: "value",
                name: "人数", // y轴名称
                nameTextStyle: {
                  // y轴名称样式
                  fontWeight: 600,
                  fontSize: 18,
                },
              },
              tooltip: {
                //鼠标放到图上的数据展示样式
                trigger: "axis",
              },
              series: [
                //每条折线的数据系列
                {
                  name: "新增治愈",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: heal_day_add,
                  type: "line",
                },
                {
                  name: "新增疑似",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: suspect_day_add,
                  type: "line",
                },
                {
                  name: "新增确诊",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: confirm_day_add,
                  type: "line",
                },
                {
                  name: "新增死亡",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: dead_day_add,
                  type: "line",
                },
              ],
            };

            let myChart = echarts.init(
              document.getElementById("line"),
              "light"
            ); // 初始化echarts, theme为light
            myChart.setOption(echartsOption); // echarts设置选项
          })
        );
    },
    buildline1() {
      let confirm_day_add = null;
      let test_day_add = null;
      let dead_day_add = null;
      let vaccinations_day_add = null;
      this.$axios
        .all([
          this.$axios.get("../../static/world/dates/confirm_day_add.json"),
          this.$axios.get("../../static/world/dates/dead_day_add.json"),
          this.$axios.get("../../static/world/dates/new_tests.json"),
          this.$axios.get("../../static/world/dates/new_vaccinations.json"),
        ])
        .then(
          this.$axios.spread((...resp) => {
            var dates = resp[0].data.data;
            confirm_day_add = resp[0].data.value;
            dead_day_add = resp[1].data.value;
            test_day_add = resp[2].data.value;
            vaccinations_day_add = resp[3].data.value;

            let echartsOption = {
              legend: {
                //图表上方的图例
                data: ["新增检测", "新增接种", "新增确诊", "新增死亡"],
              },
              xAxis: {
                type: "category",
                data: dates, // x轴数据
                name: "日期", // x轴名称
                nameTextStyle: {
                  // x轴名称样式
                  fontWeight: 600,
                  fontSize: 18,
                },
              },
              yAxis: {
                type: "value",
                name: "人数", // y轴名称
                nameTextStyle: {
                  // y轴名称样式
                  fontWeight: 600,
                  fontSize: 18,
                },
                axisLabel: {
                  margin: 4,
                },
              },
              tooltip: {
                //鼠标放到图上的数据展示样式
                trigger: "axis",
              },
              series: [
                //每条折线的数据系列
                {
                  name: "新增检测",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: test_day_add,
                  type: "line",
                },
                {
                  name: "新增接种",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: vaccinations_day_add,
                  type: "line",
                },
                {
                  name: "新增确诊",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: confirm_day_add,
                  type: "line",
                },
                {
                  name: "新增死亡",
                  // 取消折点圆圈
                  symbol: "none",
                  // 取平滑曲线
                  smooth: true,
                  data: dead_day_add,
                  type: "line",
                },
              ],
            };
            echarts.init(document.getElementById("line1")).dispose();
            let myChart = echarts.init(
              document.getElementById("line1"),
              "light"
            ); // 初始化echarts, theme为light
            myChart.setOption(echartsOption); // echarts设置选项
          })
        );
    },
    buildz() {
      var total_cases, total_deaths, total_tests, total_vaccinations;
      this.$axios
        .all([
          this.$axios.get("../../static/world/continent/total_cases.json"),
          this.$axios.get("../../static/world/continent/total_deaths.json"),
          this.$axios.get("../../static/world/continent/total_tests.json"),
          this.$axios.get(
            "../../static/world/continent/total_vaccinations.json"
          ),
        ])
        .then(
          this.$axios.spread((...resp) => {
            var continent = resp[0].data.data;
            total_cases = resp[0].data.value;
            total_deaths = resp[1].data.value;
            total_tests = resp[2].data.value;
            total_vaccinations = resp[3].data.value;
            echarts.init(document.getElementById("z")).dispose();
            let echart = echarts.init(document.getElementById("z"));
            let option = {
              tooltip: {},
              legend: {
                data: ["累计确诊", "累计检测", "累计死亡", "累计接种"],
              },
              xAxis: {
                data: continent,
                axisLabel: {
                  show: true,
                  interval: 0,
                  rotate: 0,
                },
              },
              yAxis: {
                axisLabel: {
                  formatter: (params) => {
                    return params/100000000+'亿'
                  },
                },
              },
              series: [
                {
                  name: "累计确诊",
                  type: "bar",
                  data: total_cases,
                },
                {
                  name: "累计检测",
                  type: "bar",
                  data: total_tests,
                },
                {
                  name: "累计死亡",
                  type: "bar",
                  data: total_deaths,
                },
                {
                  name: "累计接种",
                  type: "bar",
                  data: total_vaccinations,
                },
              ],
            };
            echart.setOption(option);
          })
        );
    },
  },
  mounted() {
    this.$axios
      .get("../../static/dates.json")
      .then((resp) => {
        this.dates = resp.data.dates;
      })
      .then(() => {
        if (this.$route.params.index === 1) {
          this.tab = "1";
          this.id = "line1";
        }
        this.loading = true;
        this.date = "2021-12-15";
        this.changedate(this.date);
        this.buildline();
        this.buildline1();
        this.buildz();
      });
  },
};
</script>

<style>
@import "../assets/css/bootstrap.min.css";
#line {
  width: 100vh;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
#line1 {
  width: 100vh;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
#z {
  width: 100vh;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
</style>


