<template>
  <div
    style="
      background: #00ffff;
      background-repeat: no-repeat;
      background-size: cover;
    "
  >
    <el-page-header @back="goBack" :content="country.name"> </el-page-header>

    <el-container>
      <el-card style="margin-left: 15%; width: 70%; margin-right: 15%">
        <el-descriptions
          title="疫情数据"
          direction="vertical"
          :column="3"
          border
        >
          <el-descriptions-item label="累计确诊">{{
            country.total_cases
          }}</el-descriptions-item>
          <el-descriptions-item label="累计死亡">{{
            country.total_deaths
          }}</el-descriptions-item>
          <el-descriptions-item label="累计接种">{{
            country.total_vaccinations
          }}</el-descriptions-item>
          <el-descriptions-item label="累计检测">{{
            country.total_tests
          }}</el-descriptions-item>
          <el-descriptions-item label="重症患者">{{
            country.icu_patients
          }}</el-descriptions-item>
          <el-descriptions-item label="住院人数">{{
            country.hosp_patients
          }}</el-descriptions-item>
        </el-descriptions>
        <div id="country"></div>
      </el-card>
    </el-container>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      country: null,
    };
  },

  methods: {
    goBack() {
      this.$router.push({ name: "Main", params: { index: 1 } });
    },
    buildline() {
      let confirm_day_add = new Array(8).fill(0);
      let test_day_add = new Array(8).fill(0);
      let dead_day_add = new Array(8).fill(0);
      let vaccinations_day_add = new Array(8).fill(0);
      this.$axios
        .all([
          this.$axios.get("../../static/world/json/2021-10-03.json"),
          this.$axios.get("../../static/world/json/2021-10-15.json"),
          this.$axios.get("../../static/world/json/2021-10-31.json"),
          this.$axios.get("../../static/world/json/2021-11-02.json"),
          this.$axios.get("../../static/world/json/2021-11-15.json"),
          this.$axios.get("../../static/world/json/2021-11-30.json"),
          this.$axios.get("../../static/world/json/2021-12-02.json"),
          this.$axios.get("../../static/world/json/2021-12-15.json"),
        ])
        .then(
          this.$axios.spread((...resp) => {
            for (let i in resp) {
              for (let j in resp[i].data) {
                if (resp[i].data[j].location == this.country.name) {
                  confirm_day_add[i] = resp[i].data[j].new_cases;
                  test_day_add[i] = resp[i].data[j].new_tests;
                  dead_day_add[i] = resp[i].data[j].new_deaths;
                  vaccinations_day_add[i] = resp[i].data[j].new_vaccinations;
                  break;
                }
              }
            }
          })
        )
        .then(() => {
          let echartsOption = {
            legend: {
              //图表上方的图例
              data: ["新增检测", "新增接种", "新增确诊", "新增死亡"],
            },
            xAxis: {
              type: "category",
              data: [
                "2021-10-01",
                "2021-10-15",
                "2021-10-31",
                "2021-11-01",
                "2021-11-15",
                "2021-11-30",
                "2021-12-01",
                "2021-12-15",
              ], // x轴数据
              name: "日期", // x轴名称
              nameTextStyle: {
                // x轴名称样式
                fontWeight: 600,
                fontSize: 18,
              },
            },
            yAxis: {
              type: "value",
              name: "人数", // y轴名称
              nameTextStyle: {
                // y轴名称样式
                fontWeight: 600,
                fontSize: 18,
              },
            },
            tooltip: {
              //鼠标放到图上的数据展示样式
              trigger: "axis",
            },
            series: [
              //每条折线的数据系列
              {
                name: "新增检测",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: test_day_add,
                type: "line",
              },
              {
                name: "新增接种",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: vaccinations_day_add,
                type: "line",
              },
              {
                name: "新增确诊",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: confirm_day_add,
                type: "line",
              },
              {
                name: "新增死亡",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: dead_day_add,
                type: "line",
              },
            ],
          };

          let myChart = echarts.init(
            document.getElementById("country"),
            "light"
          ); // 初始化echarts, theme为light
          myChart.setOption(echartsOption); // echarts设置选项
        });
    },
  },
  created() {
    this.country = this.$route.params.row;
    this.buildline();
  },
};
</script>

<style>
#country {
  width: 100vh;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
</style>


