<template>
  <div style="background:	#00FFFF;
    background-repeat: no-repeat;
  background-size: cover;">
    <el-page-header @back="goBack" :content="province.name"> </el-page-header>

    <el-container>
      <el-card style="margin-left: 15%; width: 70%; margin-right: 15%">
        <el-descriptions
          title="疫情数据"
          direction="vertical"
          :column="4"
          border
        >
          <el-descriptions-item label="累计确诊">{{
            province.confirm
          }}</el-descriptions-item>
          <el-descriptions-item label="累计死亡">{{
            province.dead
          }}</el-descriptions-item>
          <el-descriptions-item label="累计治愈">{{
            province.heal
          }}</el-descriptions-item>
          <el-descriptions-item label="疑似病例">{{
            province.suspect
          }}</el-descriptions-item>
          <el-descriptions-item label="新增确诊">{{
            province.confirm_day_add
          }}</el-descriptions-item>
          <el-descriptions-item label="新增疑似病例">{{
            province.suspect_day_add
          }}</el-descriptions-item>
          <el-descriptions-item label="新增死亡病例">{{
            province.dead_day_add
          }}</el-descriptions-item>
          <el-descriptions-item label="新增治愈">{{
            province.heal_day_add
          }}</el-descriptions-item>
        </el-descriptions>
        <div id="province"></div>
      </el-card>
    </el-container>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data() {
    return {
      province: null,
    };
  },

  methods: {
    goBack() {
      this.$router.push({ name: "Main" ,params:{index:0}});
    },
    buildline() {
      let confirm_day_add = [];
      let suspect_day_add = [];
      let dead_day_add = [];
      let heal_day_add = [];
      this.$axios
        .all([
          this.$axios.get("../../static/china/json/2021-10-03.json"),
          this.$axios.get("../../static/china/json/2021-10-15.json"),
          this.$axios.get("../../static/china/json/2021-10-31.json"),
          this.$axios.get("../../static/china/json/2021-11-02.json"),
          this.$axios.get("../../static/china/json/2021-11-15.json"),
          this.$axios.get("../../static/china/json/2021-11-30.json"),
          this.$axios.get("../../static/china/json/2021-12-02.json"),
          this.$axios.get("../../static/china/json/2021-12-15.json"),
        ])
        .then(
          this.$axios.spread((...resp) => {
            for (let i in resp) {
              for (let j in resp[i].data) {
                if (resp[i].data[j].province == this.province.name) {
                  confirm_day_add[i] = resp[i].data[j].confirm_day_add;
                  suspect_day_add[i] = resp[i].data[j].suspect_day_add;
                  dead_day_add[i] = resp[i].data[j].dead_day_add;
                  heal_day_add[i] = resp[i].data[j].heal_day_add;
                  break;
                }
              }
            }
          })
        )
        .then(() => {
          let echartsOption = {
            legend: {
              //图表上方的图例
              data: ["新增治愈", "新增疑似", "新增确诊", "新增死亡"],
            },
            xAxis: {
              type: "category",
              data: [
                "2021-10-01",
                "2021-10-15",
                "2021-10-31",
                "2021-11-01",
                "2021-11-15",
                "2021-11-30",
                "2021-12-01",
                "2021-12-15",
              ], // x轴数据
              name: "日期", // x轴名称
              nameTextStyle: {
                // x轴名称样式
                fontWeight: 600,
                fontSize: 18,
              },
            },
            yAxis: {
              type: "value",
              name: "人数", // y轴名称
              nameTextStyle: {
                // y轴名称样式
                fontWeight: 600,
                fontSize: 18,
              },
            },
            tooltip: {
              //鼠标放到图上的数据展示样式
              trigger: "axis",
            },
            series: [
              //每条折线的数据系列
              {
                name: "新增治愈",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: heal_day_add,
                type: "line",
              },
              {
                name: "新增疑似",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: suspect_day_add,
                type: "line",
              },
              {
                name: "新增确诊",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: confirm_day_add,
                type: "line",
              },
              {
                name: "新增死亡",
                // 取消折点圆圈
                symbol: "none",
                // 取平滑曲线
                smooth: true,
                data: dead_day_add,
                type: "line",
              },
            ],
          };

          let myChart = echarts.init(
            document.getElementById("province"),
            "light"
          ); // 初始化echarts, theme为light
          myChart.setOption(echartsOption); // echarts设置选项
        });
    },
  },
  created() {
    this.province = this.$route.params.row;
    this.buildline();
  },
};
</script>

<style>
#province {
  width: 100vh;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
</style>


