<template>
  <div>
    <el-table
      :cell-style="cellStyle"
      height="250"
      :data="list"
      style="width: 100%"
      @row-click="view"
    >
      <el-table-column prop="location" label="国家" width="180">
      </el-table-column>

      <el-table-column prop="total_cases" label="累计确诊" width="180">
      </el-table-column>

      <el-table-column prop="total_deaths" label="累计死亡"> </el-table-column>

      <el-table-column
        prop="total_vaccinations"
        label="累计接种"
      ></el-table-column>
    </el-table>

    <el-switch
      style="margin-top: 15%"
      v-model="type"
      @change="changetype"
      active-text="累计确诊"
      inactive-color="#13ce66"
      inactive-text="新增病例"
    >
    </el-switch>

    <div>
      <div id="map" ref="world"></div>
    </div>
  </div>
</template>
<script>
import echarts from "echarts"; //引入组件
import "../../node_modules/echarts/map/js/world.js"; //引入组件

export default {
  data() {
    return {
      list: [],
      confirm: [],
      confirm_day_add: [],
      nameMap: null,
      type: true,
      chart: null,
    };
  },
  mounted() {},
  methods: {
    cellStyle() {
      return "cursor:pointer;";
    },
    view(val) {
      this.$router.push({ name: "Country", params: { row: val } });
    },
    changetype(val) {
      this.initChart();
    },
    initChart() {
      this.$axios
        .get("../../static/world/map.json")
        .then((resp) => {
          this.nameMap = resp.data.namemap;
        })
        .then(() => {
          echarts.init(this.$refs.world).dispose();
          this.chart = echarts.init(this.$refs.world);
          window.onresize = echarts.init(this.$refs.world).resize;
          // 把配置和数据放这里
          this.chart.setOption({
            backgroundColor: "#02AFDB",
            title: {
              //地图显示标题
              text: "全球国家疫情地图",
              top: "30px",
              left: "center",
              textStyle: { color: "#fff" },
            },
            visualMap: {
              //图列显示柱
              type: "continuous",
              min: 0,
              left: 30,
              max: this.type == true ? 2000000 : 10000,
              text: this.type == true ? ["2000000", "0"] : ["10000", "0"],
              realtime: false,
              color: ["red", "orangered", "yellow", "lightskyblue"],
            },
            tooltip: {
              //提示框组件
              trigger: "item",
              formatter:
                this.type == true
                  ? "国家:{b}<br/>累计确诊:{c}"
                  : "国家:{b}<br/>新增病例:{c}",
            },
            series: [
              {
                type: "map",
                mapType: "world",
                // roam: true,
                mapLocation: { y: 100 },
                data: this.type === true ? this.confirm : this.confirm_day_add, //绑定数据
                // nameMap: this.nameMap,
                symbolSize: 12,
                label: {
                  normal: { show: false },
                  emphasis: { show: false },
                },
                itemStyle: {
                  emphasis: {
                    borderColor: "#fff",
                    borderWidth: 1,
                  },
                },
              },
            ],
          });

          this.chart.on("click", (params) => {
            for (let i in this.list) {
              if (this.list[i].name === params.data.name) {
                this.$router.push({
                  name: "Country",
                  params: { row: this.list[i] },
                });
                break;
              }
            }
          });
        });
    },
  },
};
</script>
<style scoped>
#map {
  width: 700px;
  height: 550px;
  top: 0px;
  margin: 10px auto;
  border: none;
}
</style>


